.footer{
  width: 100vw;

  button{
    position: absolute;
    bottom: 1rem;
    padding: 7px 25px;
    right: 1rem;
    border-radius: .4rem;
    border: 3px solid $header-outline;
    color: lighten($header-outline, 20%);
    background:none;
    font-weight: 600;
    letter-spacing: 2px;

    &:hover{
      background-color: rgba(white, $alpha: .8);
      color: $dark-text;
      cursor: pointer;
    }
  }
}